.modal-backdrop {
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  background-color: rgba(0, 0, 0, .65);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.modal-backdrop.active {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
}

.modal {
  visibility: visible;
  max-width: 414px;
  width: 100%;
  height: 100%;
  max-height: 780px;
