/* montserrat-regular - latin */
  @font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    src: url(/src/fonts/montserrat-v25-latin-regular.woff);
    src: local(''),
         url('/src/fonts/montserrat-v25-latin-regular.woff2') format('woff2'), 
         url('/src/fonts/montserrat-v25-latin-regular.woff') format('woff'); 
  }
